.container {
  background-color: white;
  padding: 20px;
  border-radius: 25px;
  border: none;
  width: 450px;
  max-height: 600px;
  overflow: auto;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.ul {
  background-color: #5f6ae263;
  padding: 20px;
  border: none;
  list-style: none;
  color: black;
  border-radius: 25px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
